.footer-container {
  background-color: #fffbef;
  padding: 2rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 24px;
  padding: 24px;
  color: rgb(0, 0, 0);
}

hr {
  display: flex;
  top: 0px;
  height: 1px;
  width: 135vmin;
  border: 0;
  border-top: 1px solid rgb(38, 38, 38);
  padding: 0;
}

.footer-subscription > p {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.footer-subscription-heading {
  margin-bottom: 24px;
  font-size: 20px;
}

.footer-subscription-text {
  margin-bottom: 24px;
  font-size: 12px;
}

.footer-input {
  padding: 8px 20px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 14px;
  margin-bottom: 16px;
  border: 1px solid rgb(0, 0, 0);
  background-color: #fffbef;
}

.footer-links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.footer-link-wrapper {
  display: flex;
}

.footer-link-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 16px;
  text-align: center;
  width: 160px;
  box-sizing: border-box;
}

.footer-link-items h2 {
  margin-bottom: 20px;
}

.footer-link-items > h2 {
  color: rgb(0, 0, 0);
}

.footer-link-items a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  margin-bottom: 8px;
}

.footer-link-items a:hover {
  color: #000000;
  transition: 0.3s ease-out;
}

.footer-email-form h2 {
  margin-bottom: 32px;
}

.footer-input::placeholder {
  color: #000000;
}

/* Social Icons */
.social-icon-link {
  color: rgb(0, 0, 0);
  font-size: 24px;
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social-logo {
  color: rgb(0, 0, 0);
  justify-self: start;
  margin-left: 0px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-self: center;
  margin-bottom: 20px;
}

.website-rights {
  color: rgb(0, 0, 0);
  margin-bottom: 20px;
}

@media screen and (max-width: 820px) {
  .footer-links {
    padding: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}
